/* For particle.js */
.bg {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #000;
}
/* ---- */

.app {
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  z-index: 1;
}

.inner-app {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  margin-top: 40px;
}

.name-container {
  display: flex;
  justify-content: center;
}

.name {
  width: 600px;
}

@media (max-width: 768px) {
  .name {
    width: 300px;
  }
  .inner-app {
    padding-top: 40px;
    margin: 0;
  }
}

.description {
  color: #eee;
  font-size: 1.2rem;
  max-width: 1400px;
  font-weight: 300;
}

@media (max-width: 768px) {
  .description {
    font-size: 1rem;
    max-width: 350px;
  }
}

.image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transform: scale(1.2);
}

.link-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

@media (max-width: 768px) {
  .link-container {
    flex-direction: column;
  }
}

.link-button {
  transition: 200ms ease-in-out;
  background-color: rgba(255, 255, 255, 0.6);
  padding: 10px 20px;
  display: flex;
  align-items: center;
  margin: 5px 20px;
  text-decoration: none;
}

@media (max-width: 768px) {
  .link-button {
    justify-content: center;
  }
}

.link-button:hover {
  background-color: rgba(255, 255, 255, 0.9);
}

.link {
  color: #000;
  font-size: 1rem;
}

.link-image {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}

.below-buttons-image {
  margin-top: 20px;
  width: 500px;
  height: 200px;
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .below-buttons-image {
    width: 350px;
    height: 150px;
    margin-bottom: 40px;
  }
}

.roadmap-image {
  width: 400px;
}

@media (max-width: 768px) {
  .roadmap-image {
    width: 350px;
  }
}